.container{
    position: relative;
    height: 100vh;
    overflow: hidden;
}

.circle{
    position: absolute;
    top:10%;
    left:5%;
    width: 10px;
    height: 10px;
    border:1px solid rgb(192, 191, 191);
    animation: rotatebox 12s ease-in infinite;
    transform: scale(0);
}

.circle:nth-child(2){
    top:10%;
    left:5%; 
    animation-delay: 2s;
}


.circle:nth-child(3){
    top:55%;
    left:3%;
    animation-delay: 2s;
}


.circle:nth-child(4){
    top:55%;
    left:3%;
    animation-delay: 8s;
}


.circle:nth-child(5){
    top:90%;
    left:12%;
    animation-delay: 8s;
}

.circle:nth-child(6){
    top:90%;
    left:12%;
    animation-delay: 12s;
}



@keyframes rotatebox{
    0%{transform: scale(0) rotate(0deg);
        opacity: 1;
    }
    100%{
        transform: scale(10) rotate(960deg);
        opacity: 0;
    }
}