
.carousel-cont{
    align-items: center;
    width: 80%;
    display: flex;
}

.carousel-cont button
{
     position: absolute;
     top:55%;
     background-color:#0a192f ;
     border-radius: 40px;
     padding: 2px;
     border: 2px solid #0a192f;
     box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.5);
}
.carousel-cont svg{
    stroke:#64ffda;
}

.left-btn{
    left:2%;
}
.right-btn{
    left: 80%;
}


.carousel-cont button:hover 
{
    background-color: #64ffda;  
    cursor: pointer;

}
.carousel-cont button:hover svg{
    stroke:#0a192f
}

@keyframes floatingButton {
    0% {transform: translateY(0);}
    50% {transform: translateY(10px);}
}
@media (max-width: 850px){
   .carousel-cont{
       height: 600px;
   }
   .right-btn{
       left: 78%;
   }
}