.experience-cont{
    color:#8892b0;
    width: 60%;
    margin-top: -1%;
    margin-left: 10%;
    animation: Entry_about_cont 1s;
}

.experience-cont ul{
    color:#64ffda;
}
.experience-cont p{
    line-height: 25px;
}
.exp-title-cont{
    display: flex;
    justify-content: space-around;
}

.exp-title-cont h2{
   font-size: 20px;
   font-weight: 600;

} 
.exp-title-cont a{
    text-decoration: none;
    color: rgb(252, 246, 245);
}

.exp-title-cont h3{
    font-size: 15px;
    font-weight: 300;
    
}

.exp-title-cont img{
    width: 20%;
    height: 20%;
    border-radius: 10px;
    box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.5);
}
.mobile-exp-cont{
    background-color: #0a192f;
    width: 100%;
    padding: 10%;
    margin-top: -20%;
    padding-top: 20%;
    box-shadow: 0px 0px 10px 4px rgba(0, 0, 0, 0.5);
    /* font-size: 20px; */
}
.mobile-img-cont{
    margin-left: 10%;
}
.mobile-exp-cont a{
    text-decoration: none;
    color: rgb(252, 246, 245);
}
.mobile-exp-cont h3{
    font-size: 15px;
    font-weight: 300;
    
}
.mobile-img-cont img{
   width: 150px;
   border-radius: 100px;
   box-shadow: 0px 0px 10px 4px rgba(0, 0, 0, 0.5);
}


@keyframes Entry_about_cont{
    0% {transform: translateY(100%);}
    100% {text-replace: translateY(0%)}
}