
.form-card-cont{
    color:#8892b0;
    width: 50%;
    position: absolute;
    top:25%;
    right:13%;
    animation: Entry_about_cont 1s;
}
.mail-content{
  position: relative;
  width: 30%;
  padding: 40px;
  font-size:18px;
  margin-top: 10%;
}
.contact-para{
    width: 75%;
    color: #0a192f;
    margin: 10%;
    text-align: left;
    line-height: 25px;
    font-weight: 600;
}

.mail-img{
    width: 60%;
    opacity: 5%;
    position: absolute;
    top:-10%;
    left:15%


}

@media (max-width:900px){
    .form-card-cont{
        width: 70%;
        right: 20%;
        /* border-radius: 0; */
    }
}

@keyframes Entry_about_cont{
    0% {transform: translateY(100%);}
    100% {text-replace: translateY(0%)}
}