.logo{
    color:white;
    padding-bottom: 15px
}

.logo-cont ,.emailId-cont{
    display: flex;
    flex-direction: column;
    align-items: center;
}
.logo-cont{
    margin-right: 10px;
}

.emailId-cont a{
    writing-mode: vertical-lr;
    letter-spacing: 5px;
    font-size: 12px;
    transition:0.5s;
    text-decoration: none;
    color:#0a192f;
    margin-top: 10px;
}

.emailId-cont:hover a{
    color: salmon;
    cursor: pointer;
    transform: translateY(5px);
}
.media-comp-cont{
    position: absolute;
    position: fixed;
    top:0;
    right:5%;
    display:flex;
    /* border: 2px solid red; */

}
.vertical-line{
    height: 30vh;
    width: 2px;
    background-color: #7d8ab1;
}


@media (max-width:720px){
    .media-comp-cont{
        bottom:0;
    }
    .logo-cont ,.emailId-cont{ 
        flex-direction: column-reverse;
        
    }
    .emailId-cont:hover a{ 
        transform: translateY(-5px);
    }
    .vertical-line{
        height: 20vh;
    }
}

