
.ProjectsCard-cont{
    width: 80%;
    background-color: #0a192f;
    box-shadow: 0px 0px 10px 4px rgba(0, 0, 0, 0.5);
    margin-left: 13%;
    animation: Entry_about_cont 1s;
    border-radius: 30px;
    display: flex;
    align-items: center;
}

.ProjectsCard-content-cont{
    color: #8892b0; 
    margin: 30px;
    width: 100%;
    
}


.ProjectsCard-content-cont p,ul{
    line-height: 30px; 
    
}
.ProjectsCard-content-cont ul{
    color: #64ffda;
}

.ProjectsCard-cont img{
   width: 40%;
   animation: Entry_about_cont 2s;
   border-radius: 10px;
   margin-right:15px;
}

@media (max-width: 850px){
    .ProjectsCard-cont{
      margin-top: 100px;

    }
   
}
@keyframes Entry_about_cont{
    0% {transform: translateY(100%);}
    100% {text-replace: translateY(0%)}
}


