
.about-cont{
    margin-left:5%;
    margin-top: -1%;
    width: 75%;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: Entry_about_cont 1s;
}

.about-verticalLine{
    top:-100%
}

.content-cont{
    text-align: left;
    line-height: 25px;
}

.content-cont p{
    color: #8892b0;
}

.img-cont{
   border-right: 1px solid salmon;
   border-bottom: 1px solid salmon;
   border-radius: 200px;
   animation: Entry_image_cont 1s;
   margin-left: -150px;
   transition: all 0.1s;
   /* box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.5); */
}
.img-cont img{
    width: 280px;
   
}

.para-cont{
   width: 85%;
}
.para-cont span{
    color: #64ffda;
}
.para-cont a{
    text-decoration: none;
    color: #64ffda;
}
.para-cont li{
    color:#64ffda ;
}


@keyframes Entry_about_cont{
    0% {transform: translateY(100%);}
    100% {text-replace: translateY(0%)}
}
@keyframes Entry_image_cont{
    0% {transform: translateY(100%) translateX(0%);}
    100% {text-replace: translateY(0%)}
}

@keyframes floatingButton {
    0% {transform: translateY(0);}
    50% {transform: translateY(-10px);}
}

@media (max-width:900px) {
    .img-cont img{
        width: 250px;
    }
    .img-cont{
        margin-left: -120px;
    }
}
@media (max-screen:720px) {
    .para-cont{
        width: 100%;
        padding-right: 0;
    }
}
