.circle-cont{
    clip-path: circle(50% at 50% 50%);
    border: 3px solid red; 
    animation: floatingButton 3s infinite ;
    /* background-color:blue; */
    /* width: 10%; */
    /* border: 1px solid red; */
}

.big-circle{
    width: 30px;
    height: 30px;
    /* top:6%;
    left:16% */
}
.medium-circle{
    width: 20px;
    height: 20px;
    /* top:12%;
    left:13% */
}
.small-circle{
    width: 10px;
    height: 10px;
    /* top:13%;
    left:17% */
}

@keyframes floatingButton {
    0% {transform: translateY(0);}
    50% {transform: translateY(-10px);}
}

@media (max-width:720px) {
    .big-circle{
        width: 25px;
        height: 25px;
        
    }
}