
.img-Cont1{
    display: flex;
    justify-content: center;
    width: 900px;
    border-radius: 200px;
    border: 1px solid red;  
    /* box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.5); */
}

.img-Cont1 img{
  width: 80%;
  border-radius: 200px;
  margin:20px;
  background-color:sandybrown;
  filter: grayscale(100%);
  box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0.5);
}
.homeLeft-cont{
    width: 30%;
    height: 100vh;
    background-color: #0a192f;
    border-bottom-right-radius: 200px;
    border-right: 2px solid salmon;
}

.homeRight-cont{
    width: 75%;
    display: flex;
}
.home-cont{
    display: flex;
    background-color: #64ffda;

   
}
.intro-cont{
    color:#0a192f ;
    text-align: left;
    margin-left: 2%;
    margin-top: 10%;
    line-height: 20px;
    
}
.intro-cont h1{
    font-size: 60px;
     
}

.intro-cont h2{
    font-size: 30px;
}

.logo-img{
    opacity: 5%;
    width: 10%;
    margin-left: 5%;
    /* box-shadow: 0px 0px 5px 2px rgba(0, 0, 0, 0.5); */
    
}

.dot{
    width: 10px;
    height: 10px;
    background-color: #8892b0;
    margin-right: 10px;
    border-radius: 10px;
}
.footer-cont{
    position: absolute;
    bottom:2%;
    display: flex;
    align-items: center;
    left:50%;
}
.footer-cont a{
    text-decoration: none;
    color: #0a192f;
    font-size: 12px;
    
    
}

.logo-and-image{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10%;
    margin-left: -14%;
    flex-direction: row;

}
@media (max-width:720px){
    .intro-cont h1{
        line-height: 60px; 
        font-size: 10vw;  
    }

      .intro-cont h2{
          font-size: 4vw;
       }

    .img-Cont1{
       width: 250px;
       margin-top: 10%;
       align-self: flex-start;
       margin-left: 5%;
    }
    .intro-cont{
        line-height: 10px;
    
    }
    .logo-and-image{
        flex-direction: column-reverse;
        margin-left: 0;
    }
  
}


