
.list-cont{
    display: flex;
    flex-direction: column;
    line-height: 80px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    letter-spacing: 2px;
    font-size: 1.2vw;
    text-transform: uppercase;
    font-weight: 900;
    margin: 0;
    text-align: right;
    margin-right: 6%;  
}
.link{
    text-decoration: none;
    color:#64ffda;
}

.list-cont li{
    list-style: none;
}

.mobile{
    flex-direction: row; 
    font-size: 2vw;
    justify-content: space-evenly;
    margin: 0;
    background-color: #0a192f;
}
