.upper-half{
    height: 25vh;
    font-family: monospace;
    font-size:5vw;
    letter-spacing:20px;
    border:1px solid rgba(0,0,0,0);
}
.title-and-circle{
  display: flex;
  align-items: center;
  margin-left: 10%;
}
.upper-half .name{
    color: transparent;
    animation: floatingButton 5s infinite ;
    margin-left: 5%;
}
.upper-half .name p{
    overflow: hidden; /* Ensures the content is not revealed until the animation */
    border-right: 2px solid #0a192f; /* The typwriter cursor */
    white-space: nowrap; /* Keeps the content on a single line */
    letter-spacing: .15em; /* Adjust as needed */
    animation: 
      typing 3s steps(30, end) 0.1s forwards,
      blink-caret .75s step-end infinite;
  }

  .upper-circle-cont{
    display: flex;
    border: 1px solid blue;
    background-color: blueviolet;
  }
  
  /* The typing effect */
  @keyframes typing {
    from { width: 0 ;color: transparent;}
    to { width: 100% ;color:#0a192f; }
  }
  
  /* The typewriter cursor effect */
  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: #0a192f; }
    step-end {border-color: transparent;}
    /* 100% {border-color: transparent;} */
  }

  @keyframes floatingButton {
    0% {transform: translateY(-0);}
    50% {transform: translateY(-10px);}
}
@media (max-width:800px) {
  .upper-half{
    font-size: 6vw;
  }
}