.form-cont{
    color:#8892b0;
    display: flex;
    flex-direction: column;
    text-align: left;
    width: 100%;
}
.form-cont input,textarea{
    background-color: transparent;
    border: none;
    border-bottom: 2px solid #8892b0 ;
    color:#8892b0;
    outline: none;
    font-size: 20px;
    
}
.form-cont input,label,textarea{
    margin: 10px;
}
.form-cont textarea{
    border:2px solid #8892b0 ;
    height: 15vh;
}
.form-cont:hover input ,textarea{
   outline: none; 
}
.form{
    width: 80%;
    margin-top: 15px;
}
.form-cont button{
     /* width: 10%; */
     background-color: transparent;
     border: none;
     border: 2px solid #8892b0;
     border-radius: 50px;
     padding: 10px;
     position: absolute;
     bottom: 15%;
     right: 10%;
     /* box-shadow: 0px 0px 10px 4px rgba(0, 0, 0, 0.5); */
}

.form-cont button:hover{
    cursor: pointer;
    border: 2px solid #64ffda;
}

.feather:hover{
    fill: #64ffda;
}

@media (max-width:900px){
    .form input{
        width: 100%;
    }
}